/*
  ---------------------
  PAGES strength
  ---------------------
*/


.strength {

    .hero {
      background: url("../img/pages/bg-hero-strength.png") 50% / cover no-repeat;
      height: 320px;
      @media #{$sp} {
        text-align: center;
        height: auto;
      }
      .containers {
        padding-top: 83px;
        @media #{$sp} {
          padding: 20px 15px;
        }
      }
      .ttl-primary {
        font-size: 36px;
        margin-bottom: 28px;
        color: #fff;
        @media #{$sp} {
          font-size: 24px;
          margin-bottom: 20px;
        }
      }
      &__lead {
        font-size: 20px;
        line-height: 30px;
        color: #fff;
        @media #{$sp} {
          font-size: 14px;
          margin-bottom: 0;
        }
        > em {
          font-size: 37px;
          font-weight: bold;
          font-style: normal;
          @media #{$sp} {
            font-size: 16px;
          }
        }
      }
    }

    .symmetry {
        .item {
            &__box {
                width: 522px;
                @media #{$sp} {
                  width: 98.5%;
                }

                &--num {
                    font-size: 22px;
                    color: #F7701B;
                    @media #{$sp} {
                      font-size: 16px;
                    }
                }
                &--ttl {
                    font-size: 26px;
                    margin-bottom: 12px;
                    @media #{$sp} {
                      font-size: 18px;
                      text-align: center;
                    }
                }
            }

            &__img {
                width: 522px;
                @media #{$sp} {
                  width: 98.5%;
                }
            }
        }
    }

    .product {

        .txt-link {
          text-align: center;
          a {
            padding-left: 35px;
            position: relative;
            font-weight: bold;
            color: #F7701B;
            text-decoration: underline;
            &::before {
              content: "";
              position: absolute;
              top: 5px;
              left: 0;
              width: 27px;
              height: 23px;
              background: url("../img/common/icon-txt-link.svg") 50% / contain no-repeat;
            }
          }
        }

        .lead {
          @media #{$sp} {
            text-align: left;
          }
        }

        .middle {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
        }

  
        .attention-list {
          margin-bottom: 44px;
          font-size: 22px;
          line-height: 33px;
          font-weight: bold;
          display: flex;
          flex-direction: column;
          justify-content: center;

          @media #{$sp} {
            font-size: 16px;
            line-height: 23px;
            text-align: left;
          }

          >* {
            display: inline-block;
            padding-left: 37px;
            position: relative;
            margin-bottom: 10px;
            +* {
                margin-bottom: 0;
            }

            &::before {
                content: "";
                position: absolute;
                background: url("../img/pages/icon-circle-check-yellow.svg") no-repeat top left / 100% auto;
                width: 25px;
                height: 25px;
                top: 0;
                bottom: 0;
                margin: auto;
                left: 0;
            }
          }

          .arrow-to-right {
            display: inline-block;
            padding-left: 37px;
            position: relative;


            &::before {
                content: "";
                position: absolute;
                background: url("../img/pages/icon-arrow-to-right.svg") no-repeat top left / 100% auto;
                width: 24px;
                height: 16px;
                top: 0;
                bottom: 0;
                margin: auto;
                left: 0;
            }
          }

          .border {
            display: inline-block;
            position: relative;

            &::after {
                content: "";
                position: absolute;
                width: 100%;
                height: 17px;
                background-color: #FFF500;
                left: 0;
                bottom: 0;
                z-index: 0;
            }

            >b {
                position: relative;
                z-index: 1;
            }
            }
  
          @media #{$sp} {
              width: 98.5%;
            }
          >.border {
              display: inline-block;
              position: relative;
  
              &::after {
                  content: "";
                  position: absolute;
                  width: 100%;
                  height: 17px;
                  background-color: #FFF500;
                  left: 0;
                  bottom: 0;
                  z-index: 0;
              }
  
              >b {
                  position: relative;
                  z-index: 1;
              }
          }
        }

        .profile {
            padding: 43px 53px 51px 82px;
            background: rgba(141,124,74,.07);
            border: 1px solid rgba(141,124,74,.1);

            @media #{$sp} {
              padding: 20px;
            }

            .item {
                margin-bottom: 35px;
                &__img {
                    width: 195px;
                    margin-right: 40px;
                    @media #{$sp} {
                      margin: 0 auto 20px;
                    }
                }

                &__box {
                    width: 683px;

                    &--ttl {
                        padding-bottom: 10px;
                        margin-bottom: 22px;
                        border-bottom: 1px solid #8D7C4A;
                        font-size: 20px;
                        @media #{$sp} {
                          text-align: center;
                        }
                        
                        b {
                            display: inline-block;
                            font-size: 26px;
                            padding-right: 6px;
                        }
                    }

                    &--lead {
                        font-size: 18px;
                        font-weight: bold;
                        line-height: 30px;
                        margin-bottom: 15px;
                        @media #{$sp} {
                          font-size: 16px;
                        }
                    }

                    &--txt {
                        font-size: 16px;
                        @media #{$sp} {
                          font-size: 14px;
                        }
                    }
                }
            }

            .item-under {
                &__box {
                    +* {
                        margin-left: 40px;
                        @media #{$sp} {
                          margin: 20px 0 0 0;
                        }
                    }

                    &--ttl {
                        font-size: 16px;
                        line-height: 30px;
                        font-weight: bold;
                        color:#8D7C4A;
                        margin-bottom: 10px;
                    }

                    &--list {
                        border-radius: 5px;
                        padding: 20px;
                        background: #fff;
                        min-height: 208px;
                        >* {
                            font-size: 14px;
                            line-height: 28px;
                        }

                        dl {
                            padding-left: 24px;
                            position: relative;
                            
                            &::before { 
                                content: "";
                                position: absolute;
                                background: url("../img/pages/icon-medal.svg") no-repeat top left / 100% auto;
                                width: 14px;
                                height: 21px;
                                top: 0;
                                left: 0;
                            }

                            +* {
                                margin-top: 15px;
                            }
                        }

                        dt {
                            font-weight: bold;
                            font-size: 14px;
                            line-height: 16px;
                            margin-bottom: 5px;
                        }

                        dd {
                            font-size: 14px;
                            line-height: 21px;
                        }
                    }
                }
            }
        }

      }

    .recommend {
        background: #f7f7f7;
        .containers {
            padding: 60px 30px 110px;
        }

        .card {
            position: relative;

            @media #{$sp} {
              margin-bottom: 80px;
            }

            &::after {
                content: "";
                position: absolute;
                width: 14px;
                height: 14px;
                border-top: solid 5px #50B4C8;
                border-right: solid 5px #50B4C8;
                left: 0;
                right: 0;
                margin: auto;
                bottom: -32px;
                transform: rotate(135deg);
                transition: all 0.1s ease-out;
            }

            .card__img {
                overflow: hidden;
            }

            &:hover {
                &::after {
                    bottom: -42px;
                    transition: all 0.1s ease-out;
                }

                .card__img {
                    > img {
                        transform: scale(1.1);
                        transition: all 0.1s ease-out;
                    }
                }
            }

            .card__box {
                background: #000;
                padding: 15px 0;
                >* {
                    font-size: 20px;
                    line-height: 30px;
                    font-weight: bold;
                    color: #fff;
                    text-align: center;
                }
            }  
        }
    }

    .about {
        .card {
            border-top: solid 5px #50B4C8;
            background: #fff;
            padding: 30px;
            margin-bottom: 30px;
            box-shadow: 0 0 10px rgba(0,0,0,0.1);
            &:last-of-type {
                margin-bottom: 0;
            }

            .card__box--ttl {
                font-size: 22px;
                line-height: 33px;
                font-weight: bold;
                margin-bottom: 30px;
            }

            .card__box--txt {
                font-size: 16px;
                line-height: 28px;
                b {
                  font-weight: bold;  
                }
            }

        }
    }
  
    .features {
      
      .card {
        margin-bottom: 40px;
        @media #{$sp} {
          margin-bottom: 20px;
        }
        &__box {
          width: calc(100% / 4 - 20px);
          padding: 30px;
          background: #fff;
          border-top: 4px solid #50B4C8;
          @media #{$sp} {
            width: 100%;
            padding: 15px;
            &:not(:last-child) {
              margin-bottom: 15px;
            }
          }
          &--img {
            width: 100px;
            height: 100px;
            margin: 0 auto 20px;
            @media #{$sp} {
              margin: 0 auto 15px;
              width: 60px;
              height: 60px;
            }
          }
          &--ttl {
            font-size: 20px;
            font-weight: bold;
            text-align: center;
            margin-bottom: 20px;
            @media #{$sp} {
              font-size: 16px;
              margin-bottom: 15px;
              height: auto;
            }
          }
          &--list {
            >* {
                padding-left: 22px;
                position: relative;
                
                &::before {
                    content: "";
                    position: absolute;
                    background: url("../img/pages/icon-check.svg") no-repeat top left  / 100% auto;
                    width: 15px;
                    height: 11px;
                    top: 10px;
                    left: 0;

                }
                
            }
          }
        }
      }
    }

    .strength {
        background: url(../img/pages/bg-strength.png) no-repeat top center / auto 100%;
        &__box {
            width: 37%;
            text-align: center;
            @media #{$sp} {
              width: 100%;
            }
            &--sub {
              font-size: 22px;
              margin-bottom: 20px;
              @media #{$sp} {
                font-size: 16px;
                margin-bottom: 15px;
              }
            }
            &--ttl {
              font-size: 32px;
              font-weight: bold;
              margin-bottom: 30px;
              @media #{$sp} {
                font-size: 18px;
                margin-bottom: 15px;
              }
            }
            .more {
              transition: all .3s;
            }
          }
          &:hover {
            opacity: 1;
            .more {
              background-position: 99% 50%;
            }
          }

        .wp-card {
            margin-top: 70px;
            padding: 40px;
            background: #fff;
            position: relative;
            box-shadow: 0 3px 6px rgba(0,0,0,0.16);
            @media #{$sp} {
              margin-top: 30px;
              padding: 20px;
            }
            &::before {
              content: "";
              position: absolute;
              top: 0;
              left: 0;
              width: 0;
              height: 0;
              border-style: solid;
              border-width: 50px 50px 0 0;
              border-color: #F7701B transparent transparent transparent;
              @media #{$sp} {
                border-style: solid;
                border-width: 25px 25px 0 0;
                border-color: #F7701B transparent transparent transparent;
              }
            }
            &__img {
              width: 32%;
              text-align: center;
              @media #{$sp} {
                width: 100%;
              }
              img {
                width: auto;
                height: 230px;
                overflow: hidden;
                position: relative;
                @media #{$sp} {
                  height: 200px;
                }
                &::before {
                  position: absolute;
                  top: 0;
                  left: -75%;
                  z-index: 2;
                  display: block;
                  content: '';
                  width: 50%;
                  height: 100%;
                  background: -webkit-linear-gradient(left, rgba(255,255,255,0) 0%, rgba(255,255,255,.3) 100%);
                  background: linear-gradient(to right, rgba(255,255,255,0) 0%, rgba(255,255,255,.3) 100%);
                  -webkit-transform: skewX(-25deg);
                  transform: skewX(-25deg);
                } 
              }
            }
            &__box {
              width: 37%;
              text-align: center;
              @media #{$sp} {
                width: 100%;
              }
              .wp-card__box--sub {
                font-size: 22px;
                margin-bottom: 20px;
                @media #{$sp} {
                  font-size: 16px;
                  margin-bottom: 15px;
                }
              }
              .wp-card__box--ttl {
                font-size: 32px;
                font-weight: bold;
                margin-bottom: 30px;
                @media #{$sp} {
                  font-size: 18px;
                  margin-bottom: 15px;
                }
              }
              .more {
                transition: all .3s;
              }
            }
            &:hover {
              opacity: 1;
              .more {
                background-position: 99% 50%;
              }
            }
          }
        }
  
    .sec-column {
      .ttl-secondary {
        margin-bottom: 40px;
        @media #{$sp} {
          margin-bottom: 20px;
        }
      }
      .grid {
        &::before {
          content: "";
          width: calc(100% / 3 - 20px);
          order: 1;
          height: 0;
          @media #{$sp} {
            width: 100%;
          }
        }
        &::after {
          content: "";
          width: calc(100% / 3 - 20px);
          height: 0;
          @media #{$sp} {
            width: 100%;
          }
        }
        .card {
          display: block;
          width: calc(100% / 3 - 20px);
          background: #fff;
          position: relative;
          &::before {
            content: "";
            position: absolute;
            bottom: 10px;
            right: 10px;
            width: 19px;
            height: 17px;
            background: url("../img/common/link-arrow-o.svg") 50% / contain no-repeat;
          }
          @media #{$sp} {
            width: 100%;
          }
          &:hover {
            opacity: 1;
            .card__img img {
              transform:scale(1.1,1.1);
            }
            .card__box--ttl {
              text-decoration: underline;
            }
          }
          &__img {
            overflow: hidden;
            img {
              aspect-ratio: 3 / 2;
              object-fit: cover;
              transition: all .3s;
            }
          }
          &__box {
            padding: 30px;
            &--cat {
              // margin-bottom: 10px;
            }
            &--ttl {
              font-weight: 700;
              margin-bottom: 15px;
              display: -webkit-box;
              -webkit-box-orient: vertical;
              -webkit-line-clamp: 2;
              overflow: hidden;
            }
            &--time {
              // font-size: 14px;
              // @media #{$sp} {
              //   font-size: 12px;
              // }
            }
          }
        }
      }
      .more {
        margin-top: 40px;
        @media #{$sp} {
          margin-top: 20px;
        }
      }
    }
  
  }