/*
  ---------------------
  PAGES energysave
  ---------------------
*/


.energysave {

    .hero {
      background: url("../img/energysave/bg-hero.webp") no-repeat 50% / cover;
      height: 450px;
      @media #{$sp} {
        background: url("../img/energysave/bg-hero-sp.webp") no-repeat 50% / cover;
        text-align: center;
      }
      .containers {
        
        @media #{$sp} {
          display: block;
          padding: 120px 15px 35px;
        }
      }
      &__cat {
        margin-bottom: 40px;
        @media #{$sp} {
          margin-bottom: 15px;
        }
        .cat {
          font-size: 18px;
          font-weight: 500;
          width: auto;
          display: inline-block;
          padding: 5px 11px;
          background: #50B4C8;
          border: 0;
          @media #{$sp} {
            width: auto;
            font-size: 14px;
            padding: 5px;
          }
        }
      }
      &__lead {
        font-size: 26px;
        font-weight: 500;
        margin-bottom: 65px;
        &.cB {
            color:#000;
        }
        @media #{$sp} {
          font-size: 18px;
          margin-bottom: 45px;
        }
        > em {
          font-size: 37px;
          font-weight: bold;
          font-style: normal;
          @media #{$sp} {
            font-size: 24px;
          }
        }
      }
      &__btn {
        .ctabtn {
          width: 400px;
          margin: 0;
          @media #{$sp} {
            margin-left: 12%;
            width: 85%;
            &:not(:last-child) {
              margin-bottom: 30px;
            }
          }
          .more.wp-thumb {
            font-size: 20px;
            width: 400px;
            @media #{$sp} {
              width: 100%;
              font-size: 14px;
            }
          }
        } 
      }
    }

    .recommend {
        background: #f7f7f7;
        .containers {
            padding: 60px 30px 110px;
        }

        .card {
            position: relative;

            @media #{$sp} {
              margin-bottom: 80px;
            }

            &::after {
                content: "";
                position: absolute;
                width: 14px;
                height: 14px;
                border-top: solid 5px #50B4C8;
                border-right: solid 5px #50B4C8;
                left: 0;
                right: 0;
                margin: auto;
                bottom: -32px;
                transform: rotate(135deg);
                transition: all 0.1s ease-out;
            }

            .card__img {
                overflow: hidden;
            }

            &:hover {
                &::after {
                    bottom: -42px;
                    transition: all 0.1s ease-out;
                }

                .card__img {
                    > img {
                        transform: scale(1.1);
                        transition: all 0.1s ease-out;
                    }
                }
            }

            .card__box {
                background: #000;
                padding: 15px 0;
                >* {
                    font-size: 20px;
                    line-height: 30px;
                    font-weight: bold;
                    color: #fff;
                    text-align: center;
                }
            }  
        }
    }

    .about {
        .card {
            border-top: solid 5px #50B4C8;
            background: #fff;
            padding: 30px;
            margin-bottom: 30px;
            box-shadow: 0 0 10px rgba(0,0,0,0.1);
            &:last-of-type {
                margin-bottom: 0;
            }

            .card__box--ttl {
                font-size: 22px;
                line-height: 33px;
                font-weight: bold;
                margin-bottom: 30px;
            }

            .card__box--txt {
                font-size: 16px;
                line-height: 28px;
                b {
                  font-weight: bold;  
                }
            }

        }
    }
  
    .product {
      .item {
        margin-bottom: 50px;
        @media #{$sp} {
          margin-bottom: 25px;
        }
        &__box {
          width: calc(90% / 2 - 20px);
          @media #{$sp} {
            width: 100%;
          }
          &:not(:last-child) {
            position: relative;
            @media #{$sp} {
              margin-bottom: 50px;
            }
            &::before {
              content: "";
              position: absolute;
              top: 0;
              bottom: 0;
              right: -50px;
              margin: auto;
              width: 64px;
              height: 40px;
              background: url("../img/top/arrow.svg") 50% / contain no-repeat;
              @media #{$sp} {
                top: auto;
                bottom: -50px;
                left: 0;
                right: 0;
                margin: auto;
                width: 30px;
                height: 54px;
                transform:rotate(90deg);
              }
            }
          }
        }
      }
      .txt-link {
        text-align: center;
        a {
          padding-left: 35px;
          position: relative;
          font-weight: bold;
          color: #F7701B;
          text-decoration: underline;
          &::before {
            content: "";
            position: absolute;
            top: 5px;
            left: 0;
            width: 27px;
            height: 23px;
            background: url("../img/common/icon-txt-link.svg") 50% / contain no-repeat;
          }
        }
      }

      .attention {
        background-color: #fff;
        background: url("../img/pages/icon-loudspeaker.png") center left 16px / 164px 120px no-repeat;
        box-shadow: 0 0 10px rgba(0,0,0,0.1);
        padding: 32px 0;
        text-align: center;
        font-size: 24px;
        line-height: 40px;
        font-weight: bold;
        width: 752px;
        margin: auto;

        @media #{$sp} {
            width: 98.5%;
            padding: 20px;
            font-size: 14px;
          }

        >.border {
            display: inline-block;
            position: relative;

            &::after {
                content: "";
                position: absolute;
                width: 100%;
                height: 17px;
                background-color: #FFF500;
                left: 0;
                bottom: 0;
                z-index: 0;
            }

            >b {
                position: relative;
                z-index: 1;
            }
        }
      }
    }
  
    .attend {

      .desc__img {
        background: #fff;
        padding: 30px;
        width: 75%;
        margin: 0 auto 40px;
        position: relative;
        @media #{$sp} {
          margin-bottom: 20px;
          padding: 15px 15px 15px 40px;
          width: 700px;
        }
        &::before {
          content: "";
          position: absolute;
          left: -65px;
          bottom: 0;
          width: 73px;
          height: 246px;
          background: url("../img/pages/lady.svg") 50% / contain no-repeat;
          @media #{$sp} {
            left: 0;
            width: 30px;
            height: 123px;
          }
        }
        &--link {
          color: #F7701B;
          text-decoration: underline;
          margin-top: 15px;
          &:hover {
            text-decoration: none;
          }
        }
      }

      .attention {
        background: #fff url("../img/pages/icon-loudspeaker.png") center left 16px / 164px 120px no-repeat;
        box-shadow: 0 0 10px rgba(0,0,0,0.1);
        padding: 52px 93px 52px 164px;
        text-align: center;
        font-size: 24px;
        line-height: 40px;
        font-weight: bold;
        width: 983px;
        margin: auto;

        @media #{$sp} {
          background: #fff url("../img/pages/icon-loudspeaker.png") center left 16px / 82px 60px no-repeat;
            width: 98.5%;
            padding: 20px;
            font-size: 14px;
            line-height: 23px;
          }
        >.border {
            display: inline-block;
            position: relative;

            &::after {
                content: "";
                position: absolute;
                width: 100%;
                height: 17px;
                background-color: #FFF500;
                left: 0;
                bottom: 0;
                z-index: 0;
            }

            >b {
                position: relative;
                z-index: 1;
            }
        }
      }
    }

    .simulation {
        .desc__img {
          background: #fff;
          padding: 60px 60px 40px;
          width: 75%;
          margin: 0 auto 40px;
          @media #{$sp} {
            margin-bottom: 20px;
            padding: 30px 30px 20px;
            width: 700px;
          }
        }
    
          .attention {
            background: #fff url("../img/pages/icon-loudspeaker.png") center left 16px / 164px 120px no-repeat;
            box-shadow: 0 0 10px rgba(0,0,0,0.1);
            padding: 52px 93px 52px 164px;
            text-align: center;
            font-size: 24px;
            line-height: 40px;
            font-weight: bold;
            width: 983px;
            margin: auto;
    
            @media #{$sp} {
              background: #fff url("../img/pages/icon-loudspeaker.png") center left 16px / 82px 60px no-repeat;
              width: 98.5%;
              padding: 20px;
              font-size: 14px;
              line-height: 23px;
            }
            >.border {
                display: inline-block;
                position: relative;
    
                &::after {
                    content: "";
                    position: absolute;
                    width: 100%;
                    height: 17px;
                    background-color: #FFF500;
                    left: 0;
                    bottom: 0;
                    z-index: 0;
                }
    
                >b {
                    position: relative;
                    z-index: 1;
                }
            }
          }
    }

    .features {
      
        .card {
          margin-bottom: 40px;
          @media #{$sp} {
            margin-bottom: 20px;
          }
          &__box {
            width: calc(100% / 4 - 20px);
            padding: 30px;
            background: #fff;
            border-top: 4px solid #50B4C8;
            @media #{$sp} {
              width: 100%;
              padding: 15px;
              &:not(:last-child) {
                margin-bottom: 15px;
              }
            }
            &--img {
              width: 100px;
              height: 100px;
              margin: 0 auto 20px;
              @media #{$sp} {
                margin: 0 auto 15px;
                width: 60px;
                height: 60px;
              }
            }
            &--ttl {
              font-size: 20px;
              font-weight: bold;
              text-align: center;
              margin-bottom: 20px;
              @media #{$sp} {
                font-size: 16px;
                margin-bottom: 15px;
                height: auto;
              }
            }
            &--list {
              >* {
                  padding-left: 22px;
                  position: relative;
                  
                  &::before {
                      content: "";
                      position: absolute;
                      background: url("../img/pages/icon-check.svg") no-repeat top left  / 100% auto;
                      width: 15px;
                      height: 11px;
                      top: 10px;
                      left: 0;
  
                  }
                  
              }
            }
          }
        }

        .wp-card {
            margin-top: 70px;
            padding: 40px;
            background: #fff;
            position: relative;
            box-shadow: 0 3px 6px rgba(0,0,0,0.16);
            @media #{$sp} {
              margin-top: 30px;
              padding: 20px;
            }
            &::before {
              content: "";
              position: absolute;
              top: 0;
              left: 0;
              width: 0;
              height: 0;
              border-style: solid;
              border-width: 50px 50px 0 0;
              border-color: #F7701B transparent transparent transparent;
              @media #{$sp} {
                border-style: solid;
                border-width: 25px 25px 0 0;
                border-color: #F7701B transparent transparent transparent;
              }
            }
            &__img {
              width: 32%;
              text-align: center;
              @media #{$sp} {
                width: 100%;
              }
              img {
                width: auto;
                height: 230px;
                overflow: hidden;
                position: relative;
                @media #{$sp} {
                  height: 200px;
                }
                &::before {
                  position: absolute;
                  top: 0;
                  left: -75%;
                  z-index: 2;
                  display: block;
                  content: '';
                  width: 50%;
                  height: 100%;
                  background: -webkit-linear-gradient(left, rgba(255,255,255,0) 0%, rgba(255,255,255,.3) 100%);
                  background: linear-gradient(to right, rgba(255,255,255,0) 0%, rgba(255,255,255,.3) 100%);
                  -webkit-transform: skewX(-25deg);
                  transform: skewX(-25deg);
                } 
              }
            }
            &__box {
              width: 37%;
              text-align: center;
              @media #{$sp} {
                width: 100%;
              }
              .wp-card__box--sub {
                font-size: 22px;
                margin-bottom: 20px;
                @media #{$sp} {
                  font-size: 16px;
                  margin-bottom: 15px;
                }
              }
              .wp-card__box--ttl {
                font-size: 32px;
                font-weight: bold;
                margin-bottom: 30px;
                @media #{$sp} {
                  font-size: 18px;
                  margin-bottom: 15px;
                }
              }
              .more {
                transition: all .3s;
              }
            }
            &:hover {
              opacity: 1;
              .more {
                background-position: 99% 50%;
              }
            }
          }
    }

    .hojo {
        .desc__img {
            width: 628px;
            margin: 0 auto 16px;
        }

        .desc__box {
            .caution {
                font-size: 16px;
                color: #E50012;
                a {
                    text-decoration: underline;
                    font-weight: bold;
                    &:hover {
                        text-decoration: none;
                    }
                }
            }
        }
    }
  
  }